.comprasion-container {
  max-width: 1300px;
  margin: 0 auto;
  font-size: $basic-font;
  td, tr {
    font-size: inherit;
  }
  td {
    padding: 10px 0px ;
    padding-left: 30px;
    width: 25vw;
    box-sizing: border-box;
    @include desktop {
      padding: 16px 0px;
    }
  }
  h1 {
    font-size: $large-font * 2;
    text-align: center;
    margin-bottom: 30px;
    @include  desktop {
      font-size: $large-font * 3;
      margin-bottom: 50px;
    }
  }
  table {
    table-layout: fixed;
  }
  .table-container {
    overflow-x: scroll;
  }
  .comprasion-products__product {
    width: 279px;
  }
  .comprasion-products__title {
    text-align: center;
    max-width: 198px;
    padding: 0 20px;
    padding-right: 40px;
  }
  .comprasion-products__img {
    margin: 0 11px;
    max-width: 220px;
    object-fit: cover;
    border-radius: 6px;
  }
  .comprasion-products__img-holder {
    padding: 0;
  }
  .comprasion-products__price {
    font-size: $large-font + 2;
    font-weight: 700;
  }
  td.comprasion-products__aside-col {
    padding-left: 0px;
    min-width: 120px;
    width: 120px;
    @include desktop {
      min-width: 199px;
      width: 199px;
    }
  }
  .comprasion-products__aside {
    padding: 4px 9px;
    display: inline-block;
    background-color: rgba($secondary-color, 1);
    border-radius: 6px;
    width: 100%;
  }
  .comprasion-products__aside_reset {
    background-color: transparent;
  }
  .btn-primary {
    color: #fff;
    font-size: $basic-font;
    font-weight: $bold-weight;
    padding: 10px 15px;
    background-color: $contrast-green;
    border-radius: 6px;
    transition: all .5s ease;
    &:hover {
      background-color: rgba($contrast-green, 1);
    }
  }
  .btn-danger {
    @extend .btn-primary;
    background-color: transparent;
    border: 1px solid $contrast-green;
    margin-left: 48px;
    &:hover {
      background-color: transparent;
      border: 1px solid rgba($contrast-green, 1);
    }
  }
  .comprasion-products__aside_fixed {
    width: 120px;
    @include desktop {
      width: 199px;
    }
  }
  input[type="button"] {
    cursor: pointer;
  }
}
.product-compare {
  .nav__list {
    clip: rect(auto, 100vw, 0, auto);
    transition: all .5s ease;
  }
  .nav_active {
    .nav__list {
      clip: rect(auto, 100vw, 1000px, auto);
    }
  }
}