.order-header {
    .header__row {
        border-bottom: $basic-border;
        padding: 0 20px;
    }
    .header__container {
        max-width: 1280px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
    }
    .header__logo {
        padding: 34px 0 25px 0;
        max-width: 187px;
    }
    .header_help-item {
        padding: 13px 19px;
        background-color: $primary-btn;
        color: $primary-color;
        font-weight: $bold-weight;
        font-size: $large-font;
        border-radius: 6px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin-bottom: 0;
        @include desktop {
            border-radius: 6px 6px 6px 6px ;
            margin-bottom: -14px;
            user-select: none;
            cursor: pointer;
        }
    }
    .header__help-title {
        color: #fff;
        padding: 0 13px;
    }
    .header__logo  {
        width: 114px;
    }
}
.contacts_header {
    left: 0;
    width: 100%;
    color: white;
    clip: rect(auto, auto, 0, auto);
    transition: all .5s ease;
}


