ul {
    list-style: none;
    margin: 0;
    padding: 0;
}
h1, h2, h3, p {
    margin: 0;
} 
a, span {
    color: $base-link-color;
    text-decoration: none;
    color: inherit;
}

.container-native {
    max-width: 1280px;
    margin: 0 auto;
    position: relative;
}
input {
    border: none;
}
//Фикс шапки для сервера
html, body {
    overflow-x: visible;
}

body {
    background: $primary-color;
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
    font-size: $basic-font;
    font-weight: $basic-weight;
    color: $base-link-color;
}
.main {
    flex-grow: 1;
}
.body-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    display: none;
    background-color: #000;
    opacity: 0;
    z-index: 1;
}

.body-overlay-active {
    display: block;
    opacity: .5;
}
.scroll-disabled {
    overflow: hidden;
    position: relative;
}
.main-slider {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;

    .main-swiper {
        z-index: 0;
        width: 100%;
        margin-bottom: 25px;
        @include desktop {
            width: 1043px;
        }
        .swiper-slide {
            img {
                object-fit: contain;
                //height: 284px;
                width: 100%;
                @include desktop {
                    height: 400px;
                    width: auto;
                }
            }
        }
        .swiper-pagination {
            z-index: 1;
            position: absolute;
            bottom: 16px;
            margin: 0 auto;
            display: flex;
            left: 50%;
            transform: translate(-50%);
        }
        .swiper-pagination-bullet {
            display: inline-block;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $pagination;
            transition: all .5s ease;
        
        }
        .swiper-pagination-bullet + .swiper-pagination-bullet {
            margin-left: 7px;
        }
    
        .swiper-pagination-bullet-active {
            transform: scale(1.5);
            background-color: $pagination-active;
        }    
    }
}
.brand-slider {
    position: static;
    align-self: center;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 0;
    margin-bottom: 40px;
    @include desktop {
        width: 1170px;
        margin-bottom: 80px;
    }
    .swiper-wrapper {
        align-items: center;
    }
    .brand-slider__slide {
        width: 120px;
        @include desktop {
            width: 12.285%;
        }
    }
    .brand-slider__slide + .brand-slider__slide {
        margin-left: 20px;
        @include desktop {
            margin-left: 30px;
        }
    }
    .swiper-button-prev, .swiper-button-next {
        z-index: 5;
        position: absolute;
        display: none;
        @include desktop {
            display: inline-block;
        }
    }
    .swiper-button-prev {
        left: 0;
        transform: scale(-1, 1);
        
    }
    .swiper-button-next {
        right: 0;
    }
}
.products {
    display: flex;
    flex-direction: column;
    padding: 0 15px;

    @include desktop {
        padding: 0;
    }
}


.products__title, .products__title-hits, .products__title-review {
    font-size: $large-font * 2;
    font-weight: $bold-weight;
    text-align: center;
    margin-bottom: 50px;
}
.products__card-row {
    display: flex;
    justify-content: space-between;
    //margin-bottom: 40px;
    flex-wrap: wrap;
}
.products__card-row + .products__load-more{
    margin-top: 60px;
}
.products__load-more {
    border: 1px solid $contrast-green;
    color: $base-link-color;
    border-radius: 6px;
    padding: 10px 15px;
    background-color: transparent;
    align-self: center;
    margin-bottom: 110px;
    cursor: pointer;
}
.tools {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    flex-wrap: wrap;
}
.tools__item {
    width: calc(50% - 20px);
    height: 190px;
    @include desktop {
        max-width: 620px;
        height: auto;
    }
}
.tools__img {
    border-radius: 6px;
    object-fit: cover;
    width: 100%;
    height: 100%;
}
.seo-text {
    padding: 0 15px;
    @include desktop {
        padding: 0;
    }
    strong {
        font-size: $large-font;
    }
}
.seo-text__title {
    margin-bottom: 15px;
    font-size: $large-font * 1.5;
    @include desktop {
        margin-bottom: 25px;
        font-size: $large-font * 2;

    }
}
.seo-text__content + .seo-text__title {
    margin-top: 30px;
    @include desktop {
        margin-top: 40px;
    }
}

.seo-text__content {
    margin-bottom: 20px;
    @include desktop {
        margin-bottom: 25px;
    }
}

//Стили, которые просил добавить стас
#column-right .tools{
    flex-wrap: wrap;
    margin-bottom: 50px;
}
#column-right .tools__item
{
    margin-bottom: 50px;
}
.lang_tr
{
    text-transform: capitalize;
}

.common-home {
    @include desktop {
        background: url('images/body__background.png') top right no-repeat;
        background-color: $primary-color;

    }

    .nav__list {
        @include desktop {
            transition: unset;
            clip: rect(auto, 100vw, 50vh, auto);
        }
    }

}
