.products__card {
    display: flex;
    flex-direction: column;
    padding: 10px 12px;
    border: 1px solid $contrast-green;
    border-radius: 6px;
    margin-bottom: 20px;
    width: 163px;
    @include desktop {
        margin-bottom: 10px;
        padding: 15px 22px;
        width: 294px;
    }
}
.card__img-holder  {
    position: relative;
    margin-bottom: 15px;
}
.card__img  {
    border-radius: 6px;
    width: 140px;
    @include desktop {
        width: 248px;
    }
}

.card__activities {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    top: 5px;
    right: 0;
    @include desktop {
        top: 15px;
    }
}
.card__stickers {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.card__sticker-new, .card__sticker-hit, .card__sticker-discount {
    width: 50px;
    padding: 5px 0;
    text-align: center;
    margin-bottom: 6px;
    border-radius: 6px 0 0 6px;
    font-size: $basic-font - 2;
    font-weight: $bold-weight;
    background-color: rgba($color: $contrast-green, $alpha: 1.0);
    @include desktop {
        font-size: $basic-font;
        width: 80px;
        padding: 8px 0;
        margin-bottom: 8px;
    }
}

.card__sticker-hit {
    background-color: #FF9900;
}
.card__sticker-discount {
    background-color: #B92525;
}

.card__activities-liked, .card__activities-comparison {
    margin: 0 6px;
    width: 25px;
    cursor: pointer;
    fill: #C4C4C4;
    transition: fill .5s ease;
    @include desktop {
        margin: 0 7px;
    }
}

.card__activities-liked {
    margin-bottom: 6px;
    @include desktop {
        margin-bottom: 10px;
    }
}

.card__activities-liked_active, .card__activities-comparison_active {
    fill: #577F2A;
}

.card__title {
    margin-bottom: 15px;
    max-width: 80%;
    font-weight: $bold-weight;
    @include desktop {
        max-width: 100%;
    }
}

.card__rate {
    display: flex;
    margin-bottom: 3px;
}
.card__rate-holder {
    align-self: center;
    margin-bottom: 13px;
    display: flex;
    align-items: center;
    fill: $darken-gray;
}

.card__rate-item + .card__rate-item {
    margin-left: 3px;

}

.card__rate-holder[data-rate='1'] {
    & >:not(:nth-child(n + 2)) {
        fill: rgba($color: $contrast-green, $alpha: 1.0);
    }
}
.card__rate-holder[data-rate='2'] {
    & >:not(:nth-child(n + 3)) {
        fill: rgba($color: $contrast-green, $alpha: 1.0);
    }
}
.card__rate-holder[data-rate='3'] {
    & >:not(:nth-child(n + 4)) {
        fill: rgba($color: $contrast-green, $alpha: 1.0);
    }
}
.card__rate-holder[data-rate='4'] {
    & >:not(:nth-child(n + 5)) {
        fill: rgba($color: $contrast-green, $alpha: 1.0);
    }
}
.card__rate-holder[data-rate='5'] {
    & >:not(:nth-child(n + 6)) {
        fill: rgba($color: $contrast-green, $alpha: 1.0);
    }
}

.card__rate-icon {
    fill: inherit;
    transition: fill .5s ease;
}

.card__rate-count {
    margin-left: 3px;
    position: relative;
    @include desktop {
        margin-left: 5px;
    }
}
.card__package {
    position: relative;
    margin-bottom: 21px;
}
.card-package-amount {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: -7px;
    margin-bottom: -7px;
    input[type='radio'] {
        appearance: none;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        margin: 0;
        border: $basic-border;
        border-radius: 6px;
        transition: all .5s ease;
        cursor: pointer;
        &:checked {
            border: 1px solid  rgba($color: $contrast-green, $alpha: 1.0);
        }
    }
}

.card__package-count {
    display: block;
    position: relative;
    width: 33px;
    padding: 2px 0;
    text-align: center;
    user-select: none;
    font-weight: $bold-weight;
    transition: all .5s ease;   
    margin-left: 7px;
    margin-bottom: 7px;  
}


.card-package-title {
    display: block;
    margin-bottom: 11px;
}
.card__package-trophy {
    position: absolute;
    right: 0;
    bottom: -5px;
    cursor: pointer;
    top: -120%;
    filter: saturate(1);
    transition: filter .5s ease;
    @include  desktop {
        top: initial;
    }
}
.card__package-trophy_silver {
    filter: brightness(0) saturate(100%) invert(92%) sepia(0%) saturate(423%) hue-rotate(192deg) brightness(89%) contrast(84%);

}
.card__package-trophy_bronze {
    filter: brightness(0) saturate(100%) invert(54%) sepia(79%) saturate(827%) hue-rotate(359deg) brightness(101%) contrast(105%);
}
.card__bottom {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    flex-grow: 1;
}
.card__price {
    display: flex;
    flex-direction: column;
}
.card__price-discount {
    color: #821F1F;
    font-size: $basic-font - 1;
    text-decoration: line-through;
}
.card__price-current {
    font-size: $basic-font;
    font-weight: $bold-weight;
    @include desktop {
        font-size: $large-font * 1.125;
    }
}
.card__buy-disabled {
    display: none;
}
.card__buy-btn {
    padding: 5px 10px;
    border: none;
    font-weight: $bold-weight;
    font-size: $basic-font - 2;
    background-color: $contrast-green;
    color: $base-link-color;
    border-radius: 6px;
    align-self: flex-end;
    @include desktop {
        padding: 8px 31px;
        font-size: $large-font ;
    }
}

