.review {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 30px;
    @include desktop {
        margin-bottom: 80px;
    }

    .review__card {
        display: flex;
        flex-direction: column;
        padding: 16px 20px 24px 20px;
        max-width: 160px;
        border: $basic-border;
        border-radius: 6px;
        margin-bottom: 20px;
        @include desktop {
            max-width: 290px;
            padding: 25px 22px;
        }
    }

    .review__img {
        max-width: 250px;
        height: 150px;
        object-fit: contain;
        object-fit: cover;
        border-radius: 6px;
        margin-bottom: 14px;
        @include desktop {
            height: auto;
        }
    }

    .review__author {
        display: flex;
        align-items: start;
        margin-bottom: 14px;
    }
    .review__author-date {
        font-size: $basic-font - 2px;
        color: $darken-gray;
    }

    .review__author-content {
        margin-left: 8px;
        display: flex;
        flex-direction: column;
        overflow: hidden;
    }


    .review__rate-count {
        margin-left: 5px;
    }

    .review__title {
        font-size: $basic-font;
        font-weight: $bold-weight;
        margin-bottom: 12px;
        @include desktop {
            font-size: $large-font;
        }
    }

    .review__body {
        font-size: $basic-font - 1;
        overflow: hidden;
    }
    .review__rate-holder {
        display: flex;
        align-items: center;
        justify-content: center;
        fill: $darken-gray;
    }
    .review__rate-item + .review__rate-item {
        margin-left: 3px;
    }
    .review__rate-holder[data-rate='1'] {
        & >:not(:nth-child(n + 2)) {
            fill: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }
    .review__rate-holder[data-rate='2'] {
        & >:not(:nth-child(n + 3)) {
            fill: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }
    .review__rate-holder[data-rate='3'] {
        & >:not(:nth-child(n + 4)) {
            fill: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }
    .review__rate-holder[data-rate='4'] {
        & >:not(:nth-child(n + 5)) {
            fill: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }
    .review__rate-holder[data-rate='5'] {
        & >:not(:nth-child(n + 6)) {
            fill: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }
    
}
