$basic-border: 1px solid #474747;
$basic-radius: 0 0 6px 6px;



//main color theme
$primary-color: #0F0F0F;
$secondary-color: #747474;
$contrast-green: rgba($color: #7BBA39, $alpha: 0.4);
$darken-gray: #474747;
$black-panther: #414141;
$weathered-stone: #C4C4C4;
$erie-black: #1F1F1E;


//link
$base-link-color: #fff;
$hover-link-color: $contrast-green;


//btn
$primary-btn: $contrast-green;


// fonts 
$basic-font: 14px;
$large-font: 16px;
$basic-weight: 300;
$bold-weight: 700;

//burger
$burger-color: #fff;

//search
$search-color: $secondary-color;

//liked after
$interactivity-border: $weathered-stone;


//mobile nav head
$nav-head-color: #1F1F1E;


//pagination
$pagination: $weathered-stone;
$pagination-active: $contrast-green;


$form-input: $black-panther;
$progress-circle: #3C5620;
$progress-circle-disabled: #414141;
$progress-line: $weathered-stone;
$input-text: $weathered-stone;

//radio
$input-radio: $contrast-green;


//order
$products-bg: #222529;
$item-bg: #1e2024;