.catalog-body {
    background: #000;
    h2 {
        font-weight: $bold-weight;
        font-size: $large-font * 1.5;
        @include desktop {
            font-size: $large-font * 2;
        }
    }
    .catalog-main__title {
        text-align: center;
        margin: 0;
        margin-bottom: 25px;
        font-weight: $bold-weight;
        @include desktop {
            margin-bottom: 50px;
        }
    }
    .catalog-descr {
        .container-native {
            max-width: 1280px;
            margin: 0 auto;
            display: flex;
            font-weight: $basic-weight + 200;
            flex-direction: column;
            justify-content: start;
            align-items: center;
            overflow-y: scroll;
            max-height: 87px;
            width: 100%;
            padding: 0 16px;
            margin-bottom: 30px;
            @include desktop {
                justify-content: space-between;
                overflow: visible;
                flex-direction: row;
                max-height: unset;
                margin-bottom: 54px;
            }
    
        }

    }
    .catalog-descr_second {
        flex-direction: column;
        max-width: 696px;
        margin: 0 auto;
        max-height: unset;
        margin-bottom: 60px;
        @include desktop {
            margin-bottom: 91px;
        }
    }
    .catalog-descr__title {
        font-weight: $bold-weight;
        max-width: 511px;
        text-align: center;
        margin: 0;
        margin-bottom: 25px;
        @include desktop {
            margin-bottom: 70px;
        }
    }
    .catalog-descr__col {
        max-width: 620px;
        margin-bottom: 21px;
    }
    .category {
        display: flex;
        justify-content: space-between;
        margin-bottom: 32px;
        overflow-y: scroll;
        padding-left: 15px;

        @include desktop {
            overflow: visible;
            padding: 0;
        }

        .category__item {
            height: 95px;
            min-width: 110px;
            background-color: #252525;
            border: 1px solid $darken-gray;
            display: flex;
            flex-direction: column;
            align-items: center;
            border-radius: 6px;
            padding: 5px;
            @include desktop {
                height: 160px;
                width: 180px;    
                padding: 10px
            }
        }
        .category__item + .category__item {
            margin-left: 10px;
        }
        .category_img {
            max-width: 56px;
            display: block;
            margin-bottom: 5px;

            @include desktop {
                max-width: 100px;
            }
        }
        .category__title-holder {
            flex-grow: 1;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .category__title {
            text-align: center;
            font-size: $basic-font - 3;
            font-weight: $bold-weight;
            @include desktop {
                font-size: $large-font;
            }
        }
    }
    .seolink {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin-bottom: 50px;
        overflow: visible;
        overflow: hidden;
        position: static;
        padding-left: 17px;
        
        .seolink__item + .seolink__item {
            margin-left: 15px;
        }
        @include desktop {
            width: 1148px;
            padding: 0;
            margin: 0 auto;
            margin-bottom: 50px;
        }
    
    }
    .seolink__item {
        display: flex;
        justify-content: center;
        width: 144px;
        padding: 10px 0;
        border-radius: 30px;
        border: $basic-border;
        cursor: pointer;
        user-select: none;
        transition: all .5s ease;
        &:hover {
            border: 1px solid $contrast-green;
        }
    }
    .seolink__prev-btn, .seolink__next-btn {
        position: absolute;
        z-index: 5;
        display: none;
        opacity: 1;
        transition: all .7s ease;
        @include desktop {
            display: block;
        }
    }
    .seolink__prev-btn {
        left: 0;
        transform: scale(-1, 1);
    }
    .seolink__next-btn {
        right: 0;
    }
    .seolink__counter {
        margin-left: 5px;
    }
    .pricing {
        width: 100%;
        margin: 0 auto;
        @include desktop {
            width: 696px;
        }
    }
    .pricing__title {
        margin-bottom: 39px;
        text-align: center;
        font-weight: $bold-weight;
    }
    .swiper-button-disabled {
        opacity: 0;
    }
}
.price-table {
    border-collapse:collapse;
    width: 100%;
    margin-bottom: 125px;
    .price-table__head-row {
        padding: 22px;
        background-color: rgba($color: $black-panther, $alpha: .5);
    }
    .price-table__head-cel--first {
        padding: 22px 0 22px 15px;
        text-align: left;
    }
    .price-table__head-cel--last {
        text-align: right;
        padding: 22px 15px 22px 0;
    }
    .price-table__cel--first {
        padding: 23px 0 15px 14px;
        text-align: left;
    }
    .price-table__cel--last {
        padding: 23px 14px 15px 0;
        text-align: right;
    }
    .price-table__body {
        & >:nth-child(2n) {
            background-color: $black-panther;
        }
    }
}

.faq {
    width: 100%;
    margin: 0 auto;
    margin-bottom: 100px; 
    @include desktop {
        max-width: 696px;

    }
    .faq__item {
        cursor: pointer;
        user-select: none;
    }
    .faq__title {
        text-align: center;
        margin-bottom: 40px;
        font-weight: $bold-weight;
    }

    .faq__item-title {
        display: flex;
        padding: 14px 40px 14px 35px;
        justify-content: space-between;
        align-items: center;
        background-color: $black-panther;
        border-radius: 6px;
        margin-bottom: 25px;
        
        &::after {
            content: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.0008 10.3326L9.03713 5.59904C8.95561 5.52052 8.85845 5.45803 8.75125 5.41518C8.64404 5.37233 8.52893 5.34997 8.41253 5.34938C8.29614 5.34879 8.18078 5.36999 8.0731 5.41175C7.96541 5.45351 7.86755 5.51501 7.78514 5.5927C7.62004 5.748 7.52667 5.95874 7.52542 6.1789C7.52418 6.39905 7.61515 6.61072 7.77848 6.76769L12.1192 10.9564L7.73148 15.0592C7.56638 15.2145 7.47301 15.4253 7.47176 15.6454C7.47051 15.8656 7.56149 16.0773 7.72482 16.2342C7.80603 16.3134 7.90305 16.3765 8.01026 16.42C8.11748 16.4634 8.23275 16.4863 8.3494 16.4874C8.46605 16.4875 8.58158 16.4658 8.68928 16.4234C8.79697 16.381 8.89471 16.3189 8.97681 16.2406L13.9938 11.5576C14.0838 11.48 14.1557 11.3856 14.2052 11.2805C14.2546 11.1753 14.2805 11.0616 14.2811 10.9465C14.2818 10.8314 14.2572 10.7175 14.209 10.6118C14.1607 10.5061 14.0898 10.4111 14.0008 10.3326V10.3326Z' fill='%23F1F1F1'/%3E%3C/svg%3E%0A");
            display: inline-block;
            right: 9px;
            height: 22px;
            width: 21px;
            transform: rotate(90deg);
        }        
    }
    .faq__dropdown {
        max-width: 620px;
        margin-left: 42px;
        max-height: 0;
        overflow: hidden;
        transition: all .5s ease;
    }
    .faq__dropdown__list {
        margin-bottom: 23px;
    }
    //ACTIVE CLASS
    .faq__accordion_active {
        .faq__dropdown {
            max-height: 700px;
        }
    }
}
