.body-order {
    background: #0F0F0F;
}

.order-title {
    margin-top: 30px;
    margin-bottom: 40px;
    text-align: center;
    @include desktop {
        margin-bottom: 90px;
        margin-top: 120px;   
        text-align: left;
    }
}
.contacts_header {
    clip: rect(auto, auto, 0, auto);
}

.order__title-holder {
    padding-bottom: 27px;
}

.order {
    display: flex;
    flex-direction: column;

    @include desktop {
        flex-direction: row;
        justify-content: space-between;
    }

    .order__row {
        display: flex;
    }


    .order__completed {
        background-color: $primary-color;
        padding: 5px 0;
        display: flex;
        align-items: baseline;
        transition: all .5s ease;
        transform: translateX(-120%);
        opacity: 0;
        position: absolute;
    }

    .order__completed-body {
        max-width: 440px;
    }
    .order__completed-button {
        display: flex;
        align-items: center;
        cursor: pointer;
        user-select: none;
        color: rgba($contrast-green, 1);
        &:before {
            content: url("data:image/svg+xml,%3Csvg width='15' height='15' viewBox='0 0 15 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M13.75 4.52501C13.7505 4.44276 13.7347 4.36122 13.7036 4.28507C13.6725 4.20892 13.6267 4.13966 13.5688 4.08126L10.9188 1.43126C10.8604 1.37333 10.7911 1.32751 10.715 1.2964C10.6388 1.2653 10.5573 1.24953 10.475 1.25001C10.3928 1.24953 10.3112 1.2653 10.2351 1.2964C10.1589 1.32751 10.0897 1.37333 10.0313 1.43126L8.26251 3.20001L1.43126 10.0313C1.37333 10.0897 1.32751 10.1589 1.2964 10.2351C1.2653 10.3112 1.24953 10.3928 1.25001 10.475V13.125C1.25001 13.2908 1.31586 13.4497 1.43307 13.567C1.55028 13.6842 1.70925 13.75 1.87501 13.75H4.52501C4.61247 13.7548 4.69994 13.7411 4.78177 13.7099C4.8636 13.6786 4.93795 13.6306 5.00001 13.5688L11.7938 6.73751L13.5688 5.00001C13.6258 4.93944 13.6723 4.86972 13.7063 4.79376C13.7123 4.74394 13.7123 4.69358 13.7063 4.64376C13.7092 4.61467 13.7092 4.58535 13.7063 4.55626L13.75 4.52501ZM4.26876 12.5H2.50001V10.7313L8.70626 4.52501L10.475 6.29376L4.26876 12.5ZM11.3563 5.41251L9.58751 3.64376L10.475 2.76251L12.2375 4.52501L11.3563 5.41251Z' fill='%237BBC38' fill-opacity='0.5'/%3E%3C/svg%3E%0A");
            display: inline-block;
            width: 15px;
            height: 15px;
            margin-right: 5px;
        }
    }


    .order__col:first-of-type {
        margin-left: 16px;
        margin-right: 28px;
        margin-bottom: 70px;
        @include desktop {
            margin: 0;
        }
    }

    .order__col:last-of-type {
        margin-bottom: 70px;
        @include desktop {
            margin: 0;
        }
    }


    .order__personal-data {
        display: flex;
        flex-direction: column;
        width: 100%;
        overflow: hidden;
        max-height: 1000px;
        transition: all .5s ease;
        position: relative;
        @include desktop {
            margin-left: 33px;
            padding-bottom: 35px;
            //width: 370px
    
        }
    }
    .order__payment {
        @extend .order__personal-data;
        width: 473px;
        max-height: 67px;
    }
    
    .order__progress-stage {
        position: relative;
        height: 100%;
        &:after {
            content: '';
            width: 1px;
            height: 100%;
            background-color: $progress-line;
            position: absolute;
            bottom: -20px;
            left: 50%;
        }
    }

    .order__progress-circle {
        height: 40px;
        width: 40px;
        border-radius: 50%;
        background-color: $progress-circle-disabled;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        z-index: 5;
        cursor: pointer;
        user-select: none;
        transition: all .5s ease;
    }
    .order__progress-circle_active {
        background-color: $progress-circle;
    }
    .order__title {
        margin: 0 0 15px 15px;
        @include desktop {
            margin: 0 0 15px 5px;
        }
    }

    .order__input {
        padding: 13px 15px;
        color:  $input-text;
        background-color: $form-input;
        border-radius: 6px;
        margin-bottom: 30px;
        width: 100%;
        @include desktop {
            width: 370px;
        }
        &:focus {
            outline: none;
        }
    }
    .order__user-label {
        font-size: $large-font;
        transition: all .5s ease;
        white-space: nowrap;
    }
    .order__user-label:first-of-type {
        margin-bottom: 8px;
        @include desktop {
            margin: 0;
        }
    }

    .order__user-label:last-of-type {
        @include desktop {
            margin-left: 14px;
        }
    }
    .order__user-sel {
        margin-bottom: 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        align-self: center;
        @include desktop {
            flex-direction: row;
            align-items: flex-start;
            align-self: flex-start;
        }
    }
    #order__user-new,  #order__user-regular{
        appearance: none;
        opacity: 0;
        margin: 0;
        position: absolute;
        left: -9999px;

        &:checked + .order__user-label {
            color: $hover-link-color;
        }
    }
    #order__submit {
        align-self: start;
        padding: 10px 15px;
        border: none;
        border-radius: 6px;
        background-color: rgba($color: $primary-btn, $alpha: .4);
        color: $base-link-color;
        margin-bottom: 20px;
        cursor: pointer;
    }
    
    input[type="radio"] {
        appearance: none;
        width: 12px;
        height: 12px;
        border: 1px solid $input-radio;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        margin: 1px;
        flex-shrink: 0;
        &:after {
            content: '';
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            opacity: 0;
            background-color: $input-radio;
            transition: all .5s ease;
        }
    
        &:checked {
            &:after {
                opacity: 1;
            }
        }
    }
    .order__row:last-of-type {
        .order__progress-stage {
            &::after {
                display: none;
            }
        }
    }
    .order__completed_active {
        opacity: 1;
        transform: translateX(0);
        position: static;
    }



}
.order__delivery {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding-bottom: 35px;
    max-height: 1000px;
    overflow: hidden;
    position: relative;
    transition: all .5s ease;
    @include desktop {
        width: auto;
        margin-left: 33px;
        max-height: 67px;
    }
    .order__delivery-sel {
        display: flex;
        flex-direction: column;
        @include desktop {
            margin-bottom: 25px;
            flex-direction: row;
            align-items: center;
        }
    }
    .order__delivery-item {
        display: flex;
        align-items: center;
        margin-bottom: 12px;
        @include desktop {
            margin-bottom: 0;
        }
    }
    .order__delivery-item:last-of-type {
        margin-bottom: 25px;
        @include desktop {
            margin-bottom: 0;
        }
    }
    .order__delivery-label {
        margin-left: 11px;
        display: flex;
        align-items: center;
    }
    .order__descr {
        display: flex;
        align-items: center;
        position: relative;
        top: 1px;
        margin-left: 3px;
        &:hover {
            .order__descr-text {
                clip: rect(auto, 500px, 500px, auto);
            }
        }
    }
    .order__descr-text {
        display: block;
        position: absolute;
        width: 144px;
        top: calc(100% + 3px);
        left: -3px;
        padding: 10px 15px;
        background-color: $black-panther;
        clip: rect(auto, 0, 0, auto);
        transition: all .5s ease;
    }
    #order__ukr-post, #order__ukr-post-international {
        @include desktop {
            margin-left: 22px;
        }
    }
    .order__input_last {
        margin-bottom: 2px;
    }
}

.order__package {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    .order__package-label {
        margin-left: 11px;
    }
    .order__descr-text {
        margin-left: 8px;
    }
}

#order__package-checkbox {
    appearance: none;
    margin: 0;
    width: 20px;
    height: 20px;
    border-radius: 6px;
    background-color: $contrast-green;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &::after {
        content: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' viewBox='0 0 417 417' style='enable-background:new 0 0 417 417;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0%7Bfill:%23fff;%7D%0A%3C/style%3E%3Cpath class='st0' d='M159.7,364.3c-4,4-9.4,6.2-15.1,6.2s-11.1-2.2-15.1-6.2L9.4,244c-12.5-12.5-12.5-32.7,0-45.2l15.1-15.1 c12.5-12.5,32.7-12.5,45.2,0l75.1,75.1L347.4,56.1c12.5-12.5,32.7-12.5,45.2,0l15.1,15.1c12.5,12.5,12.5,32.7,0,45.2L159.7,364.3z' /%3E%3C/svg%3E%0A");
        display: block;
        width: 12px;
        position: absolute;
        opacity: 0;
        transition: .3s ease;
        margin-bottom: -3px;
    }
    &:checked {

        &::after {
            opacity: .7;
        }
    }

}

#order__delivery-submit {
    @extend #order__submit;
}

.order__payment {
    .order__payment-item {
        display: flex;
        margin-bottom: 24px;
    }

    .order__payment-label {
        display: flex;
        flex-direction: column;
        margin-left: 25px;
        font-size: $large-font;
    }
    .order__payment-descr {
        font-size: $basic-font;
        margin-top: 5px;
    }
    #order__payment-submit {
        @extend #order__submit;
    }
}

.order-products {
    width: 100%;
    background-color: $products-bg;
    transition: all .5s ease;
    @include desktop {
        width: 620px;
    }
    
    .order-products__head-title {
        font-size: $large-font;
        @include desktop {
            font-size: $large-font * 1.5;
        }
    }
    .order-products__warn-body {
        font-size: $basic-font - 3;
        @include desktop {
            font-size: $basic-font;
        }
    }
    
    .order-products__img {
        border-radius: 6px;
        max-width: 100px;
        max-height: 100px;
        object-fit: cover;
    }
    .order-products__item  {
        display: flex;
        padding: 40px 20px;
        opacity: 1;
        transition: all .5s ease;
    }
    .order-products__head {
        padding: 30px 20px 25px 20px;
    }
    .order-products__warn  {
        padding: 13px 20px;
        background-color: #D46C69;
    }
    .order-products__body {
        margin-left: 25px;
        font-size: $basic-font;
        @include desktop {
            font-size: $large-font + 2;
        }
    }
    .order-products__title {
        font-size: $large-font + 2;
        color: rgba($color: $contrast-green, $alpha: 1.0);
        margin-bottom: 8px;
        display: block;
        @include desktop {
            font-size: $large-font;
        }
    }
    .order-products__descr {
        display: flex;
        flex-direction: column;
        margin-bottom: 12px;
        font-size: $basic-font - 3;
        @include desktop {
            font-size: $basic-font;
            display: block;
        }
    }
    .order-products__count {
        margin-bottom: 4px;
        @include desktop {
            margin-left: 76px;
            margin-bottom: 0;
        }
    }
    .order-products__amount {
        display: flex;
        align-items: center;
        position: relative;
        width: 88px;
        font-size: $basic-font;
        @include desktop {
            width: 144px;
            font-size: $large-font + 2;
        }
    }
    .order-products__amount-input {
        padding: 8px 0;
        text-align: center;
        width: inherit;
        background-color: #2B2E31;
        color: #fff;
        overflow: hidden;
        border-radius: 6px;
        -moz-appearance: textfield;
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
        }
        @include desktop {

        }

        &:focus {
            outline: none;
        }
    }
    .order-products_btn {
        border: none;
        background: transparent;
        position: absolute;
        color: #fff;
        //font-size: $large-font + 2;
        cursor: pointer;
        user-select: none;
        &_plus {
            right: 0;
            padding: 12px;
        }
        &_minus {
            left: 0;
            padding: 12px;
        }
    }
    .order-products__remove-item {
        cursor: pointer;
    }


    .order-products__end-holder {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;
        flex-grow: 1;
    }
    .order__remove-icon {
        cursor: pointer;
    }
    .order-products__summary {
        padding: 25px;
    }
    .order-products__summary-row {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 12px;
        font-size: $basic-font;
        @include desktop {
            font-size: $large-font + 2;
        }
    }
    .order-products__summary-row:last-of-type {
        padding: 14px 0;
        margin-bottom: 0;
        font-size: $large-font;
        @include desktop {
            font-size: $large-font * 1.5;
        }
    }
    .order-products__summary-cost {
        color: rgba($color: $contrast-green, $alpha: 1.0);
    }
}


.order .order-hidden {
    margin-bottom: 0;

    .order__completed {
        transition-delay: 2s;
        transition: all .5s ease;
        visibility: visible;
        opacity: 1;
    }
}

