.footer {
    position: relative;

    .footer__row {
        border-top: $basic-border;
    }

    .container-native {
        position: static;
    }

    .footer__row_bottom {
        border: unset;
    }

    .container_middle {
        display: flex;
        flex-direction: column;
        width: 100%;

        @include desktop {
            flex-direction: row;
            padding: 46px 0 57px 62px;
        }
    }

    .container_bottom {
        display: flex;
        justify-content: center;
        flex-direction: column-reverse;
        align-items: center;

        @include desktop {
            justify-content: space-between;
            flex-direction: row;
        }
    }

    .footer__subscription,
    .footer__socials {
        display: flex;
        align-items: center;
        flex-direction: column;

        @include desktop {
            flex-direction: row;
        }
    }

    .footer__nav {
        display: flex;
        flex-direction: column;
        padding: 26px 34px;
        padding-bottom: 0;
        @include desktop {
            flex-direction: row;
            padding: 0;
        }
    }

    .footer__subscription-descr {
        max-width: 252px;
        font-weight: $basic-weight;
        margin-bottom: 16px;

        @include desktop {
            margin-bottom: 0;
        }

    }

    .footer__subscription-holder {
        display: flex;
        align-items: stretch;
        height: 35px;

        @include desktop {
            margin: 25px 0 20px 0;
            margin-left: 26px;
        }
    }

    .footer__subscription-input {
        width: 180px;
        padding: 10px 0 13px 19px;
        background-color: $search-color;
        border-radius: 6px 0 0 6px;
        border: none;

        &::placeholder {
            color: rgba($color: #000, $alpha: 0.1)
        }

        &:focus {
            outline: none;
        }

        @include desktop {
            width: 250px;
        }
    }

    .footer__subscription {
        justify-content: center;
        padding: 16px 0 28px 0;

        @include desktop {
            margin-left: 165px;
        }
    }

    .footer__subscription-submit {
        padding: 10px 6px;
        border: none;
        background-color: $contrast-green;
        border-radius: 0 6px 6px 0;
        margin-left: 5px;
        color: $base-link-color;
        font-weight: $bold-weight;
        cursor: pointer;
        transition: background-color, .5s ease-out;
        &:hover {
            background-color: rgba($color: $contrast-green, $alpha: 1.0);
            
        }

        @include desktop {
            padding: 10px 17px 14px;
        }
    }

    .footer__socials {
        flex-direction: row;
        align-self: center;
        padding-top: 47px;
        padding-bottom: 13px;
        @include desktop {
            margin-left: 202px;
            padding: 0;
        }
    }

    .footer__social-item {
        width: 30px;
        height: 30px;
        cursor: pointer;
    }

    .footer__social-item+.footer__social-item {
        margin-left: 15px;
    }

    .footer__about {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 32px;

        @include desktop {
            align-items: start;
            padding-top: 0;
        }
    }

    .footer__logo {
        max-width: 187px;
        margin-bottom: 16px;
    }

    .footer__contact-us {
        display: block;
        padding: 17px 77px;
        border-radius: 6px;
        border: 1px solid $primary-btn;
        font-weight: $bold-weight;
        margin-bottom: 15px;
        text-align: center;

        @include desktop {
            margin-bottom: 7px;
        }
    }

    .footer_help-item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $contrast-green;
        border-radius: 6px;
        padding: 14px 16px;
        cursor: pointer;
        user-select: none;
        transition: background-color .5s ease-out;
        &:hover {
            background-color: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }

    .footer__help-title {
        padding: 0 13px;
        color: $base-link-color;
        font-size: $large-font;
        font-weight: $bold-weight;
    }

    .footer__nav {
        @include desktop {
            margin-left: 152px;
        }
    }

    .footer__nav-item+.footer__nav-item {
        @include desktop {
            margin-left: 126px;
        }
    }

    .footer_nav-title {
        font-size: $large-font - 2;
        font-weight: $bold-weight - 200;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 17px;
        border: 1px solid $primary-btn;
        border-radius: 6px;
        position: relative;
        margin-bottom: 10px;

        //
        &::after {
            content: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.0008 10.3326L9.03713 5.59904C8.95561 5.52052 8.85845 5.45803 8.75125 5.41518C8.64404 5.37233 8.52893 5.34997 8.41253 5.34938C8.29614 5.34879 8.18078 5.36999 8.0731 5.41175C7.96541 5.45351 7.86755 5.51501 7.78514 5.5927C7.62004 5.748 7.52667 5.95874 7.52542 6.1789C7.52418 6.39905 7.61515 6.61072 7.77848 6.76769L12.1192 10.9564L7.73148 15.0592C7.56638 15.2145 7.47301 15.4253 7.47176 15.6454C7.47051 15.8656 7.56149 16.0773 7.72482 16.2342C7.80603 16.3134 7.90305 16.3765 8.01026 16.42C8.11748 16.4634 8.23275 16.4863 8.3494 16.4874C8.46605 16.4875 8.58158 16.4658 8.68928 16.4234C8.79697 16.381 8.89471 16.3189 8.97681 16.2406L13.9938 11.5576C14.0838 11.48 14.1557 11.3856 14.2052 11.2805C14.2546 11.1753 14.2805 11.0616 14.2811 10.9465C14.2818 10.8314 14.2572 10.7175 14.209 10.6118C14.1607 10.5061 14.0898 10.4111 14.0008 10.3326V10.3326Z' fill='%23F1F1F1'/%3E%3C/svg%3E%0A");
            display: inline-block;
            position: absolute;
            right: 9px;
            height: 22px;
            width: 21px;
            transform: rotate(90deg);
        }

        @include desktop {
            margin-bottom: 16px;
            border: none;
            display: block;
            border-radius: unset;
            padding: 0;
            font-size: $large-font + 4;

            &::after {
                display: none;
            }
        }

    }

    .footer__list {
        margin-left: 18px;
        max-height: 0;
        overflow: hidden;
        transition: all .5s ease;

        @include desktop {
            margin-left: 0;
            max-height: unset;
        }
    }

    .footer_list-item {
        margin-bottom: 15px;
    }
    .footer_list-link {
        transition: all .5s ease;
        &:hover {
            color: rgba($color: $contrast-green, $alpha: 1.0);
        }
    }

    .container_bottom {
        margin-bottom: 16px;
    }

    .footer__credits {
        display: flex;
        align-items: center;
        margin-bottom: 30px;

        @include desktop {
            margin-bottom: 0;
        }
    }

    .footer__credits-item {
        max-width: 40px;
    }

    .footer__credits-item+.footer__credits-item {
        margin-left: 40px;
    }

    .footer__policy {
        margin-bottom: 12px;

        @include desktop {
            margin-bottom: 0;
        }
    }

    .contacts_footer {
        left: 0;
        border-radius: 6px 6px 0 0;
        width: 100%;
        z-index: 1;
        clip: rect(auto, auto, 0, auto);
        transition: all .5s ease;
        @include desktop {
            top: unset;
            bottom: 100%;
        }
    }

    .footer__active-class {
        .footer__list {
            max-height: 500px;
        }

        .contacts_footer {
            clip: rect(auto, auto, 700px, auto);
        }
    }
}