.header {
    position: sticky;
    width: 100%;
    z-index: 20;
    top: 0;
    background-color: $primary-color;
    margin: 0;
    //transition: transform .5s ease;


    .header__banner {
        display: none;

        @include desktop {
            display: block;
            width: 100%;
            border-bottom: $basic-border;
        }
    }

    .header__banner-img {
        width: 100%;
        object-fit: cover;
        height: 40px;
        position: relative;
    }

    .header__container {
        display: flex;
        align-items: center;
        max-width: 1280px;
        margin: 0 auto;
    }

    .header__row {
        border-bottom: $basic-border;
        position: relative;
    }

    .header__row_first {
        display: none;

        @include desktop {
            display: block;
        }
    }


    .header__container_top {
        justify-content: space-between;
    }

    .header__container_bottom {
        position: relative;
        align-items: center;
        justify-content: flex-end;
        padding: 17px 15px;

        @include desktop {
            padding: 0;
        }

    }

    .header__logo {
        padding: 10px 0px;
    }

    .header__logo-img {
        max-width: 220px;
        display: block;
    }

    .header__activity-list {
        display: flex;
    }

    .header__activity-item {
        cursor: pointer;
        user-select: none;
    }

    .header__activity-link {
        transition: all .5s ease;

        &:hover {
            color: $contrast-green;
        }
    }

    .header__activity-item+.header__activity-item {
        margin-left: 26px;
    }


    .header__activity-item_arrow {
        display: flex;
        align-items: center;
        cursor: pointer;
        user-select: none;

        &:after {
            content: url("data:image/svg+xml,%3Csvg width='7' height='4' viewBox='0 0 7 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.5 4L0.468911 0.25L6.53109 0.25L3.5 4Z' fill='white'/%3E%3C/svg%3E%0A");
            display: inline-block;
            margin-left: 5px;
            top: 5px;
        }
    }

    &__lang-dropdown {
        position: absolute;
        background-color: $darken-gray;
        border-radius: 0 0 6px 6px;
        z-index: 1;
        top: 100%;
    }

    &__lang-item {
        padding: 15px 11px 6px 8px;
    }

    &__lang-link {
        transition: all .5s ease;
        color: $base-link-color;

        &:hover {
            color: $contrast-green;
        }
    }


    &__lang-item_last {
        position: relative;
        padding: 6px 11px 15px 8px;

        &::before {
            content: '';
            display: block;
            width: 70%;
            left: calc(30% / 2);
            height: 1px;
            background-color: $secondary-color;
            top: 0;
            position: absolute;
        }
    }

    &__search-mobile {
        width: 25px;
        height: 25px;
        filter: invert(100%);

        @include desktop {
            display: none;
        }
    }

    &__search-mobile-icon {
        width: 100%;

        @include desktop {
            display: none;
        }
    }


    &__search-holder {
        height: 35px;
        align-items: center;
        //mobile
        position: fixed;
        width: 100vw;
        padding: 0 15px;
        justify-content: center;
        left: 99999px;
        z-index: 2;
        background-color: $primary-color;
        transform: translateY(-250%);
        transition: transform .5s ease;

        @include desktop {
            position: relative;
            display: flex;
            width: 523px;
            left: unset;
            margin: 13px 0;
            padding: 0;
            transform: unset;
        }
    }

    &__search-input {
        height: 100%;
        width: 100%;
        background: transparent;
        transition: all .5s ease;
        background-color: $search-color;
        border-radius: 6px;
        padding: 8px 20px;
        color: white;   
        //mobile
        &:focus {
            outline: none;
        }


        @include desktop {
            border-radius: 6px 0 0 6px;
        }
    }

    &__search-btn {
        position: absolute;
        right: calc(8.33% / 2);
        top: 0;
        padding: 8px;
        height: 100%;
        background-color: #7D7D7D;
        transition: all .5s ease;
        border-radius: 0 6px 6px 0;
        cursor: pointer;

        @include desktop {
            position: relative;
            right: 0;


        }
    }

    &__liked,
    &__community,
    &__cart,
    &__comparison {
        display: flex;
        align-items: center;
        position: relative;
        user-select: none;
        cursor: pointer;

        @include desktop {
            padding: 16px 25px;

            &::after {
                content: '';
                display: block;
                position: absolute;
                right: 0;
                height: 36px;
                width: 1px;
                background-color: $interactivity-border;
            }

        }

    }

    &__liked,
    &__community,
    &__comparison {
        display: none;

        @include desktop {
            display: flex;
        }
    }

    &__comparison.active {
        &::before {
            content: '';
            display: block;
            position: absolute;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: rgba($color: $contrast-green, $alpha: 1.0);
            right: 20px;
            top: 16px;
        }
    }
    &__liked.active {
        &::before {
            content: '';
            display: block;
            position: absolute;
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: rgba($color: $contrast-green, $alpha: 1.0);
            right: 18px;
            top: 12px;
        }
    }

    &__cart {

        @include desktop {
            margin-left: 0;
        }
    }

    &__cart-counter {
        position: absolute;
        color: rgba($color: $contrast-green, $alpha: 1.0);
        font-weight: $bold-weight;
        font-size: $basic-font - 2px;
        opacity: 0;
        transform: translateY(-50%);
        top: -6px;
        right: -9px;

        @include desktop {
            top: 10px;
            right: 16px;
        }
    }

    &__cart-counter_active {
        opacity: 1;
        transform: translateY(0);
    }

    &__account {
        padding: 16px 0 16px 30px;
        display: none;
        cursor: pointer;
        user-select: none;

        @include desktop {
            display: block;
        }
    }

    &__account-title {
        margin-left: 7px;
    }

    &__cooperation-dropdown {
        position: absolute;
        width: 188px;
        background-color: $darken-gray;
        z-index: 1;
        border-radius: 0 0 6px 6px;
        top: 100%;
    }

    &__cooperation-item {
        padding: 5px 13px;
    }

    &__cooperation-item_first {
        padding-top: 13px;
    }

    &__cooperation-link {
        transition: color .5s ease;
    }

    &__cooperation-item {
        &:hover {
            .header__cooperation-link {
                color: $hover-link-color;
            }
        }
    }

    &__cooperation-item_last {
        padding-bottom: 11px;

    }

    &__cooperation-item_fisrt {
        padding-top: 13px;
    }

    &__cooperation-dropdown,
    &__lang-dropdown {
        clip: rect(auto, auto, 0, auto);
        transition: all .5s ease;
    }

    &__community-contacts_dropdown {
        clip: rect(auto, auto, 0, auto);
        transition: all .5s ease;
    }
    .nav__about-dropdown {
        max-height: 0;
        overflow: hidden;
        transition: all .5s ease;
    }
    .nav__about-dropdown_active {
        .nav__about-dropdown {
            max-height: 700px;
        }
    }

    .nav__dropdown-about-item {
        margin-left: 38px;
        padding: 5px 0;
        &:first-of-type {
            padding-top: 12px;
        }
        &:last-of-type {
            padding-bottom: 16px;
        }
    }

    .nav_active {
        .nav__list {
            clip: rect(auto, 100vw, 1000px, auto);
        }
    }

}


.active-dropdown {
    [class$="dropdown"] {
        clip: rect(auto, auto, 700px, auto);
    }
}

.nav {
    position: absolute;
    left: 15px;
    align-self: auto;
    display: flex;
    align-items: center;
    font-size: $large-font;
    cursor: pointer;
    user-select: none;
    -webkit-tap-highlight-color: transparent;

    @include desktop {
        bottom: 0;
        left: 0;
        position: absolute;
        width: 237px;
        display: block;
    }

    &__title {
        font-size: $large-font;
        font-weight: 700;
        margin-left: 22px;
    }

    &__wrapper {
        padding: 16px 21px;
        background-color: $contrast-green;
        border-radius: 6px 6px 0px 0px;
        align-items: center;
        display: none;
        transition: background-color .5s ease-out;

        &:hover {
            background-color: rgba($color: $contrast-green, $alpha: 1.0);
        }

        @include desktop {
            display: flex;
        }
    }

    &__burger {
        width: 16px;
        position: relative;

        &:before,
        &:after {
            content: '';
            position: relative;
            display: block;
            background-color: $burger-color;
            width: 16px;
            height: 2px;
        }

        span {
            display: block;
            background-color: $burger-color;
            width: 16px;
            height: 2px;
            margin-bottom: 4px;
            position: relative;
        }

        &::before {
            margin-bottom: 4px;
        }
    }

    &__burger-mobile {
        display: inline-block;

        @include desktop {
            display: none;
        }
    }

    &__logo-mobile {
        width: 114px;
        display: inline-block;
        margin-left: 15px;

        @include desktop {
            display: none;
        }
    }

    &__list {
        position: fixed;
        left: 0;
        top: 0;
        width: 83.333vw;
        background-color: $darken-gray;
        z-index: 100;
        height: 100vh;
        overflow-y: scroll;
        overflow-x: hidden;
        transform: translateX(-110%);
        transition: transform .5s ease;
        @supports (-webkit-touch-callout: none) {
            padding-bottom: 85px;
        }

        @include desktop {
            display: block;
            position: absolute;
            top: 100%;
            width: 100%;
            height: auto;
            transform: translateX(0);
            z-index: 5;
            overflow: visible;
            clip: rect(auto, 100vw, 0, auto);
            transition: clip .5s ease;
            padding: 0;
        }
    }

    &__mobile-head {
        background-color: $erie-black;
        border-bottom: $basic-border;
        display: flex;
        justify-content: space-between;
        padding: 10px 15px 10px 24px;

        @include desktop {
            display: none;
        }
    }

    &__logo-mobile-img {
        width: 160px;
        max-width: initial;
    }

    &__lang {
        position: relative;
        left: 0;

    }

    &__lang-title {
        &:after {
            content: url("data:image/svg+xml,%3Csvg width='7' height='4' viewBox='0 0 7 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.5 4L0.468911 0.25L6.53109 0.25L3.5 4Z' fill='white'/%3E%3C/svg%3E%0A");
            display: inline-block;
            margin-left: 5px;
            top: 5px;
        }
    }

    &__lang-dropdown {
        background-color: $erie-black;
        position: absolute;
        border-radius: $basic-radius;
        z-index: 1;
        clip: rect(auto, auto, 0, auto);
        transition: clip .5s ease;
        left: 0;
    }

    &__lang-item {
        padding: 15px 10px 6px 10px;
    }

    &__lang-item_last {
        padding: 6px 10px 10px 10px;
    }

    &__authorization {
        display: flex;
        padding: 15px 0 17px 24px;

        @include desktop {
            display: none;
        }
    }

    &__authorization-icon {
        margin-right: 9px;
    }

    &__authorization-item-registration {
        display: flex;
        align-items: center;
        margin-left: 9px;

        &::before {
            content: '';
            display: block;
            height: 16px;
            width: 1px;
            background-color: $contrast-green;
            margin-right: 9px;
        }
    }

    &_authorization-item-registration {
        display: flex;
        align-items: center;
        margin-left: 9px;

    }

    &__interactivity {
        @include desktop {
            display: none;
        }
    }

    &__about {
        @include desktop {
            display: none;
        }
    }

    &__item,
    &__cart,
    &__liked,
    &__comparison {
        position: relative;
        display: flex;
        align-items: center;
        padding: 6px 10px 6px 17px;
        transition: all .5s ease;
        background-color: $darken-gray;

        &:hover {
            background-color: rgba($color: $contrast-green, $alpha: 1.0);

            .nav__dropdown {
                clip: rect(auto, 100vw, 700px, auto);
            }
        }
    }

    &__item {
        position: static;
    }

    &__nav__item_first {
        padding-top: 8px;
    }

    &__item_last {
        padding-bottom: 23px;

        @include desktop {
            padding-bottom: 10px;
        }
    }

    &__cart {
        position: relative;
        padding-top: 20px;

        &::before {
            content: '';
            display: block;
            width: calc(100% - 34px);
            height: 1px;
            position: absolute;
            top: 0;
            left: 0;
            margin: 0 17px;
            background-color: $secondary-color;
        }
    }

    &__comparison {
        position: relative;
        padding-bottom: 20px;

        &::after {
            content: '';
            display: block;
            width: calc(100% - 34px);
            height: 1px;
            position: absolute;
            bottom: 0;
            left: 0;
            margin: 0 17px;
            background-color: $secondary-color;
        }

    }

    &__about-item {
        padding: 9px 10px 9px 17px;
        transition: color .5s ease;

        &:hover {
            .nav__about-link {
                color: $hover-link-color;
            }

        }
    }

    &__about-link {
        transition: color .5s ease;
    }

    &__about-link_arrow {
        &:after {
            content: url("data:image/svg+xml,%3Csvg width='7' height='4' viewBox='0 0 7 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M3.5 4L0.468911 0.25L6.53109 0.25L3.5 4Z' fill='white'/%3E%3C/svg%3E%0A");
            display: inline-block;
            margin-left: 8px;
        }
    }

    &__about-item_first {
        padding-top: 20px;
    }

    &__about-item_last {
        padding-bottom: 24px;
    }


    &__interactivity-title {
        margin-left: 15px;
    }

    &__comparison-icon,
    &__cart-icon,
    &__liked-icon {
        width: 20px;
        height: 20px;

    }



    &__item_icon {
        &::before {
            content: url("data:image/svg+xml,%3Csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12.1317 2.64645C12.3351 2.45118 12.6648 2.45118 12.8682 2.64645L18.0766 7.64645C18.2255 7.78945 18.2701 8.0045 18.1895 8.19134C18.1089 8.37818 17.9189 8.5 17.7083 8.5H15.8407L20.1599 12.6464C20.3089 12.7894 20.3534 13.0045 20.2728 13.1913C20.1922 13.3782 20.0023 13.5 19.7916 13.5H16.0218L21.1667 17.6159C21.3352 17.7507 21.3977 17.9723 21.323 18.1702C21.2484 18.3681 21.0526 18.5 20.8333 18.5H15.1041V21C15.1041 21.2761 14.8709 21.5 14.5833 21.5C14.2956 21.5 14.0624 21.2761 14.0624 21V18C14.0624 17.7239 14.2956 17.5 14.5833 17.5H19.3947L14.2499 13.3841C14.0814 13.2493 14.0189 13.0277 14.0936 12.8298C14.1682 12.6319 14.364 12.5 14.5833 12.5H18.5342L14.215 8.35355C14.066 8.21055 14.0215 7.9955 14.1021 7.80866C14.1827 7.62182 14.3726 7.5 14.5833 7.5H16.4509L12.4999 3.70711L8.54902 7.5H10.4166C10.6273 7.5 10.8172 7.62182 10.8978 7.80866C10.9784 7.9955 10.9339 8.21055 10.7849 8.35355L6.46568 12.5H10.4166C10.6359 12.5 10.8317 12.6319 10.9063 12.8298C10.981 13.0277 10.9185 13.2493 10.75 13.3841L5.60518 17.5H10.4166C10.7043 17.5 10.9374 17.7239 10.9374 18V20C10.9374 20.2761 10.7043 20.5 10.4166 20.5C10.129 20.5 9.89578 20.2761 9.89578 20V18.5H4.16661C3.94733 18.5 3.75154 18.3681 3.67689 18.1702C3.60224 17.9723 3.66473 17.7507 3.83318 17.6159L8.97804 13.5H5.20828C4.99762 13.5 4.80771 13.3782 4.72709 13.1913C4.64648 13.0045 4.69104 12.7894 4.83999 12.6464L9.15921 8.5H7.29161C7.08095 8.5 6.89104 8.37818 6.81043 8.19134C6.72981 8.0045 6.77437 7.78945 6.92333 7.64645L12.1317 2.64645Z' fill='%23C4C4C4'/%3E%3C/svg%3E%0A");
            display: inline-block;
            width: 25px;
            height: 24px;
            margin-right: 17px;
        }

    }
    &__item_arrow {
        &::after {
            content: url("data:image/svg+xml,%3Csvg width='22' height='21' viewBox='0 0 22 21' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.0008 10.3326L9.03713 5.59904C8.95561 5.52052 8.85845 5.45803 8.75125 5.41518C8.64404 5.37233 8.52893 5.34997 8.41253 5.34938C8.29614 5.34879 8.18078 5.36999 8.0731 5.41175C7.96541 5.45351 7.86755 5.51501 7.78514 5.5927C7.62004 5.748 7.52667 5.95874 7.52542 6.1789C7.52418 6.39905 7.61515 6.61072 7.77848 6.76769L12.1192 10.9564L7.73148 15.0592C7.56638 15.2145 7.47301 15.4253 7.47176 15.6454C7.47051 15.8656 7.56149 16.0773 7.72482 16.2342C7.80603 16.3134 7.90305 16.3765 8.01026 16.42C8.11748 16.4634 8.23275 16.4863 8.3494 16.4874C8.46605 16.4875 8.58158 16.4658 8.68928 16.4234C8.79697 16.381 8.89471 16.3189 8.97681 16.2406L13.9938 11.5576C14.0838 11.48 14.1557 11.3856 14.2052 11.2805C14.2546 11.1753 14.2805 11.0616 14.2811 10.9465C14.2818 10.8314 14.2572 10.7175 14.209 10.6118C14.1607 10.5061 14.0898 10.4111 14.0008 10.3326V10.3326Z' fill='%23F1F1F1'/%3E%3C/svg%3E%0A");
            display: inline-block;
