        &::before {
            content: url("data:image/svg+xml,%3Csvg width='25' height='24' viewBox='0 0 25 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12.1317 2.64645C12.3351 2.45118 12.6648 2.45118 12.8682 2.64645L18.0766 7.64645C18.2255 7.78945 18.2701 8.0045 18.1895 8.19134C18.1089 8.37818 17.9189 8.5 17.7083 8.5H15.8407L20.1599 12.6464C20.3089 12.7894 20.3534 13.0045 20.2728 13.1913C20.1922 13.3782 20.0023 13.5 19.7916 13.5H16.0218L21.1667 17.6159C21.3352 17.7507 21.3977 17.9723 21.323 18.1702C21.2484 18.3681 21.0526 18.5 20.8333 18.5H15.1041V21C15.1041 21.2761 14.8709 21.5 14.5833 21.5C14.2956 21.5 14.0624 21.2761 14.0624 21V18C14.0624 17.7239 14.2956 17.5 14.5833 17.5H19.3947L14.2499 13.3841C14.0814 13.2493 14.0189 13.0277 14.0936 12.8298C14.1682 12.6319 14.364 12.5 14.5833 12.5H18.5342L14.215 8.35355C14.066 8.21055 14.0215 7.9955 14.1021 7.80866C14.1827 7.62182 14.3726 7.5 14.5833 7.5H16.4509L12.4999 3.70711L8.54902 7.5H10.4166C10.6273 7.5 10.8172 7.62182 10.8978 7.80866C10.9784 7.9955 10.9339 8.21055 10.7849 8.35355L6.46568 12.5H10.4166C10.6359 12.5 10.8317 12.6319 10.9063 12.8298C10.981 13.0277 10.9185 13.2493 10.75 13.3841L5.60518 17.5H10.4166C10.7043 17.5 10.9374 17.7239 10.9374 18V20C10.9374 20.2761 10.7043 20.5 10.4166 20.5C10.129 20.5 9.89578 20.2761 9.89578 20V18.5H4.16661C3.94733 18.5 3.75154 18.3681 3.67689 18.1702C3.60224 17.9723 3.66473 17.7507 3.83318 17.6159L8.97804 13.5H5.20828C4.99762 13.5 4.80771 13.3782 4.72709 13.1913C4.64648 13.0045 4.69104 12.7894 4.83999 12.6464L9.15921 8.5H7.29161C7.08095 8.5 6.89104 8.37818 6.81043 8.19134C6.72981 8.0045 6.77437 7.78945 6.92333 7.64645L12.1317 2.64645Z' fill='%23C4C4C4'/%3E%3C/svg%3E%0A");
            display: inline-block;
            width: 25px;
            height: 24px;
            margin-right: 17px;
        }

        &:after {
            content: 'NEW';
            color: #FF9900;
            position: relative;
            top: -5px;
            margin-left: 2px;
            vertical-align: text-top;
            font-size: 9px;
            font-weight: 700;
        }
    }


    &__dropdown {
        width: 83.333vw;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 2;
        height: 100vh;
        background-color: $darken-gray;
        transform: translateX(-110%);
        transition: transform .5s ease;

        @include desktop {
            position: absolute;
            width: 254px;
            height: auto;
            left: 100%;
            top: 0px;
            clip: rect(auto, 100vw, 0, auto);
            transition: clip .5s ease;
            background-color: $secondary-color;
            transform: translateX(0);
            height: 100%;
            background-color: $darken-gray;
        }
    }

    .nav__dropdown-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
