.contacts {
    width: 257px;
    position: absolute;
    top: 100%;
    left: 100%;
    background-color: $darken-gray;
    border-radius: 0px 0px 6px 6px;
    font-weight: $bold-weight - 200;

    &__list {
        padding: 10px 0;
    }

    &__item {
        display: flex;
        align-items: center;
        padding: 5px 14px;
    }

    &__title {
        margin-left: 21px;
    }

    &__title_flex {
        display: flex;
        flex-direction: column;
    }

    &__descr {
        font-weight: $basic-weight - 300;
        font-size: $basic-font - 2;
        color: #C4C4C4;
    }
}

.search-dropdown {
    position: absolute;
    top: 100%;
    width: calc(100% - 30px);
    padding: 5px 20px 5px 20px;
    background-color: $darken-gray;
    clip: rect(auto, auto, 0, auto);
    transition: clip 0.7s ease;
    z-index: -1;
    @include desktop {
        width: 100%;
    }

    .search-dropdown__item {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
    }

    .search-dropdown__item-container {
        margin-left: 9px;
    }

    .search-dropdown__item-title {
        display: block;
        margin-bottom: 10px;
    }

    .search-dropdown__item-price-discount {
        text-decoration: line-through;
        color: $weathered-stone;
        font-size: $basic-font - 3;
    }

    .search-dropdown__item-price-discount+.search-dropdown__item-price-current {
        margin-left: 5px;
    }

    .search-dropdown__total-results {
        font-size: $basic-font - 3;
    }

    .search-dropdown__item-img {
        border-radius: 6px;
    }

}

.header__search-holder_active {
    transform: translateY(0);
    transition: transform .5s ease;
    left: 0;

    .header__search-input {
        border-radius: 6px 6px 0 0;
        @include desktop {
            border-radius: 6px 0 0 0;
        }
    }
    .header__search-btn {
        border-radius: 0 6px 0 0;
    } 
    .search-dropdown {
        clip: rect(auto, auto, 100vh, auto);
        transition-delay: .7s;
        transition: clip .7s ease;
    }

}


    .catalog-body .header {
        margin-bottom: 27px;

        .nav__list {
            clip: rect(auto, 100vw, 100vh, auto);
            transition: all .5s ease;

            @include desktop {
                clip: rect(auto, 100vw, 0, auto);
            }
        }

        //active class
        .header_active {
            .nav__list {
                clip: rect(auto, 100vw, 700px, auto);
            }
        }

        .transform_active {
            transform: translateX(0);
        }
    }

    .header__cart-icon {
        margin-left: 38px;
        width: 25px;    
        @include desktop {
            width: auto; 
            margin-left: 0;
        }
    }


    .button_search {
        appearance: none;
        position: absolute;
    }

    .wrap_fb3d{
        z-index: 5 !important;
    }